.loader{
    border: 1px solid yellow;
    background-color: rgba(0, 0, 0, 0.2);
    position: absolute;
    height: 100%;
    width: 100%;
    left:0;
	right:0;
	top:0;
	bottom:0;
    margin:auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loader_container{
    height: 70px;
    width: 130px;
    border: 1px solid red;
}

.loading-dots{
    /* display: flex; */
    /* justify-content: space-between; */
    /* align-items: center; */
    height: 70px;
    width: 130px;
}

.dot-flashing {
    margin: 20% auto;
    position: relative;
    width: 20px;
    height: 20px;
    border-radius: 100%;
    background-color: #9880ff;
    color: #9880ff;
    animation: dot-flashing 1s infinite linear alternate;
    animation-delay: 0.5s;
  }
  .dot-flashing::before, .dot-flashing::after {
    content: "";
    display: inline-block;
    position: absolute;
    top: 0;
  }
  .dot-flashing::before {
    left: -50px;
    width: 20px;
    height: 20px;
    border-radius: 100%;
    background-color: #9880ff;
    color: #9880ff;
    animation: dot-flashing 1s infinite alternate;
    animation-delay: 0s;
  }
  .dot-flashing::after {
    left: 50px;
    width: 20px;
    height: 20px;
    border-radius: 100%;
    background-color: #9880ff;
    color: #9880ff;
    animation: dot-flashing 1s infinite alternate;
    animation-delay: 1s;
  }
  
  @keyframes dot-flashing {
    0% {
      background-color: #9880ff;
    }
    50%, 100% {
      background-color: rgba(152, 128, 255, 0.2);
    }
  }